<template>
    <div class="menu">
    <!-- Hamburger Menu Button -->
        <span class="nav-slideout-opener" v-on:click="toggle">
            <i v-if="isOpen===false" class="pi pi-bars"></i>
            <transition enter-active-class="animated rotateIn">
                <i v-if="isOpen" class="pi pi-bars"></i>
            </transition>
        </span>
        <transition name="slide" type="animation">
            <div 
                class="sidenav"
                v-if="isOpen">
                <!-- Close Icon -->
                <span class="x-icon" v-on:click="toggle"> 
                    <transition appear enter-active-class="animated rotateIn">
                        <i v-if="isOpen" class="pi pi-times"></i>
                    </transition>
                </span>
                <!-- Nav Menu Items -->
                <ul>
                    <li class="nav-slideout-item"
                        v-for="item in menu" :key="item">
                            <a href="#">
                                {{item}}
                            </a>
                    </li>
                </ul>
            </div>
        </transition>
    </div>
</template>

<script>

    export default {
        data: function () {
            return {
                isOpen: false,
                menu: [
                        'NEWS', 'NEW INVENTORY', 'USED INVENTORY', 
                        'CHARGING', 'FIND US', 'SEMI',
                        'CARBON IMPACT', 'SUPPORT'
                        ]
            }
        },
        methods: {
            open() {
                this.isOpen = true
            },
            close() {
                this.isOpen = false
            },
            toggle() {
                // console.log('button clicked');
                if (this.isOpen) {
                    this.close();
                } else {
                    this.open();
                }
            }
        }
    }
</script>

