    /* Side Menu Animation */
    .slide-enter {
        opacity: 0;
        /* transform: translateY(20px) */
    }
    .slide-enter-active {
        animation: slide-in 0.35s ease-out forwards;
        transition: opacity .5s;
    }
    .slide-leave {
        /* opacity: 1; */
    }
    .slide-leave-active {
        animation: slide-out 0.25s ease-out forwards;
        transition: opacity 3s;
        opacity: 0;
    }
